.App {
  font-family: sans-serif;
  text-align: center;
}

* {
  font-family: montserrat;
  margin-left: 20px;
  margin-right: 20px;
}

h1 {
  margin-top: 40px;
  margin-left: 6px;
}

#myBtn {
  background: linear-gradient(
      264.95deg,
      rgba(255, 252, 252, 0.2) 0%,
      rgba(255, 252, 252, 0) 54.06%
    ),
    linear-gradient(
      267.12deg,
      rgba(251, 254, 83, 0.3) 48.01%,
      rgba(255, 255, 255, 0) 92.65%
    ),
    #ee520f;

  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 65px;
  font-style: normal;
  font-weight: normal;
  color: white;
  border: none;
  cursor: pointer;
  width: 160px;
  font-size: 1.05rem;
  height: 35px;
  border-radius: 20px;
}

#result {
  height: auto;
  width: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid grey;
  border-radius: 10px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-left: 20px;
  margin-right: 20px;
  background-color: rgb(18, 14, 38);
  color: #1ee0e0;
}

#user_image {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  border: 1px solid #1ee0e0;
  padding: 5px;
}

#search {
  font-size: 14px;
  line-height: 20px;
  height: 32px;
  width: 30vw;
  margin-left: 6px;
  margin-right: 15px;
  padding: 5px 12px;
  z-index: 100;
  outline: none;
  border-radius: 6px;
  box-shadow: 0 0 0 1px rgb(0 0 0 / 5%), 0 2px 4px 1px rgb(0 0 0 / 9%);
  border: 1px solid #d0d7de;
  -webkit-writing-mode: horizontal-tb !important;
  background-color: #f6f8fa;
  vertical-align: middle;
  cursor: text;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

input[type="text"]:focus {
  /* 	width: 50% !important; */
  border: 2px solid #218bff !important;
  background: white !important;
  box-shadow: #58cced 0px 7px 15px 0px !important;
}

#dataname {
  color: white;
  font-size: 36px;
}

#databio {
  color: #dddddd;
}

/*for mobile devices*/
