    font-size: 1.05rem;
    height: 35px;
    border-radius: 20px;
  }

  #result {
    margin-left: 5px !important;
    padding-left: 10px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    border: 1px solid grey;
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 75vw !important;
    background-color: rgb(18, 14, 38);
    color: #1ee0e0;
  }

  #user_image {
    height: 120px;
    width: 120px;
    border-radius: 50%;
    border: 1px solid #1ee0e0;
    padding: 5px;
  }

  #search {
    font-size: 18px;
    line-height: 20px;
